export default {
	name: "PopupBottom",
	props: {
		//是否需要mask
		mask: {
			type: Boolean,
			default: true
		},
		//控制显示
		show: {
			type: Boolean,
			default: false
		},
		//背景颜色
		bgcolor: {
			type: String,
			default: 'transparent'
		},
	},
	methods: {
		handleClose() {
			if (!this.show) {
				return;
			}
			this.$emit('close');
		}
	}
}
